.is-hidden {
  visibility: hidden;
  opacity: 0;
  pointer-events: none;
}

.backdrop {
  width: 100%;
  height: 100%;
  position: fixed;
  top: 0;
  background: rgba(0, 0, 0, 0.2);
  transition: opacity 250ms, visibility 250ms;
  left: 0;
  overflow-y: scroll;
}
@media screen and (min-width: 768px) {
  .backdrop {
    overflow-y: hidden;
  }
}

.img__cart {
  width: 100%;
  height: 357px;
  margin-top: 48px;
  border-radius: 5px;
  
  @include tablet {
    display: inline;
    width: 264px;
    height: 374px;
    margin-top: 40px;
    }
    @include desktop {
      width: 375px;
      height: 478px;
      margin-top: 40px;
    } 
}

.modal {
  width: 100%;
  min-height: 609px;
  background-repeat: 10;
  position: absolute;
  top: 20px;
  left: 0;
  background: #FFFFFF;
  box-shadow: 0px 1px 3px rgba(0, 0, 0, 0.12), 0px 1px 1px rgba(0, 0, 0, 0.14), 0px 2px 1px rgba(0, 0, 0, 0.2);
  border-radius: 4px;

  display: flex;
flex-direction: column;
align-items: flex-start;

  @include tablet {
    position: absolute;
    width: 704px;
    height: 552px;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    flex-direction: row;
}
 
@include desktop {
  position: absolute;
  max-width: 806px;
  min-height: 558px;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  flex-direction: row;

}
}
.modal__close {
  width: 20px;
  height: 20px;
  border-radius: 50%;
  position: absolute;
  top: 8px;
  right: 8px;
  background: #FFFFFF;
  border: none;
  fill: #000000;
}
.modal__close:hover .modal__icon {
  fill: #000000;
}

.modal__icon {
  width: 14px;
  height: 14px;
  justify-content: center;
  align-items: center;
  fill: #000000;
  fill: currentColor;
  // transition: fill 250ms cubic-bezier(0.4, 0, 0.2, 1);
}

.modal__title {
margin-top: 20px;
font-family: 'Roboto';
text-transform: uppercase;
font-weight: 500;
font-size: 20px;
line-height: 1.15;
color: var(--primary-text-color);
@include tablet {
    margin-top: 40px;
    margin-bottom: 20px;
}  
@include desktop {
  margin-top: 40px;
  margin-bottom: 20px;
    font-size: 30px;
    line-height: 1.17;
}
}

.modal__text {
  margin-top: 20px;
font-family: 'Roboto';
text-transform: uppercase;
font-weight: 500;
font-size: 12px;
line-height: 1.33;
color: var(--primary-text-color);
}

.text {
  margin-top: 8px;
font-family: 'Roboto';
font-weight: 500;
font-size: 12px;
line-height: 1.67;
color: var(--primary-text-color);
}

.position {

@include tablet {
 margin-left: 68px; 
 margin-right: 72px;
}
@include desktop {
margin-left: 16px; 
margin-right: 12px;
}
}

.modal__btn {
  margin-top: 20px;
  margin-bottom: 40px;
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
}

