.btn--modal {
@include button-reset();
font-weight: 500;
font-size: 12px;
line-height: 1.33;
text-align: center;
text-transform: uppercase;
transition: all 0.3s ease;
color: var(--primary-text-color);
padding: 13px 14px;
border-radius: 5px;
border: 1px solid var(--primary-text-color);
min-width: 110px;
min-height: 44px;

@include hover {
  border-color: var(--accent-color);
}

&:not(:last-child) {
  margin-right: 20px;
  @include tablet {
    margin-right: 15px;
    }
    @include desktop {
      margin-right: 15px;
    }
}

@include tablet {
min-width: 125px;
}
@include desktop {
min-width: 136px;
}
}
