:root {
  --primary-font: "Roboto", sans-serif;
  --grey-100: #f7f7f7;
  --grey-900: #545454;
  --primary-text-color: #000;
  --secondary-text-color: #545454;
  --accent-color: #ff6b08;
  --second--accent-color: #ff001b;
  --white-text-color: #fff;
  --error-text-color: #ff001b;
  --transparent: rgba(0, 0, 0, 0);
  --footer-text-color: var(--grey-900);
  --bg-light: var(--grey-100);
  --transition-timing: 250ms cubic-bezier(.4, 0, .2, 1);
}

.is-hidden {
