  border-radius: 50em;
  flex-shrink: 0;
  align-items: center;
  padding: 3px 0;
  display: flex;
  position: relative;
}

.theme-switch__track {
  width: 100%;
  height: 100%;
  color: rgba(0, 0, 0, 0);
  -webkit-user-select: none;
  -ms-user-select: none;
  user-select: none;
  border-radius: inherit;
  z-index: 1;
  cursor: pointer;
  background-color: #c9c9c9;
  position: absolute;
  top: 0;
  left: 0;
}

.theme-switch__marker {
  background-color: var(--accent-color);
  width: 20px;
  height: 20px;
